@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,300;1,200;1,300&display=swap);
*{
    margin:0;
    padding:0;
    box-sizing:border-box;
    font-family: 'Josefin Sans', sans-serif;
}
.header{
    width: 100%;
    line-height: 100px;
    background-color: #f5ba13;
    display: flex;
    justify-content:start;
    align-items:center; 
    padding-left: 50px;
    color: #fff;
    box-shadow: 5px 3px 15px -5px rgba(0, 0, 0, 0.5);

}



.main_note{
    width:500px;padding: 15px 10px 5pxx 10px;
    margin:  20px auto;
}
.my_notes h1{
    color: #34495e;
}
.my_notes p{
    color: gray;
}
form{
    min-height:50px;
    color: #ecf0f1;
    padding: 15px 10px 5px 10px;
    box-shadow: 3px 5px 15px -5px rgba(0, 0, 0,0.5);
    border-radius:10px;
    position: relative;
}


input{

    width: 100%;
    line-height: 30px;
    background:transparent;
    border :none;
    outline:none;
    font-size:1.5rem;
    margin:10px 0;
    font-weight : bold;

}
textarea{
    width: 100%;
    line-height: 50px;
    background: transparent;
    border:none;
    outline: none;
    font-size:1rem;
    margin: 10px 0;
}
.MuiButton-root{
    width:30px;
    height: 50px;
    position:absolute;
    bottom : -28px;
    left:400px;
    cursor:pointer;
    background-color:#fff !important;
    box-shadow: 5px 5px 15px -5px rgba(0, 0, 0,0.5);
    border-radius: 50% !important;
}

.MuiButton-root .plus_sign{
  font-size: 45px !important;
  font-weight: 900;
  color: #f4b400;
  cursor: pointer;
  z-index: 222;
  background:transparent;
}
.MuiButton-root:hover{
    background-color: #f4b400 !important;
}

.MuiButton-root:hover .plus_sign{
  color: #fff;background:transparent;  
}

.note{
    background: #fff;border-radius: 7px;
    box-shadow: 0 2px 5px #ccc;
    padding: 10px;
    width: 240px;
    margin: 16px;
    float: left;

}
.note h1{
    font-size: 1.1em;
    margin-bottom:6px;
}
.note p{
    font-size: 1.1em;
    margin-bottom: 10px;
    white-space: pre-wrap;
    word-wrap: break-word;
}
.note .btn{
    position:relative;
    float:right;margin-right: 10px;
    color: #f4b400;
    border:none;
    width: 50px;
    height: 50px;
    background:transparent;
    cursor: pointer;
    outline:none;
    box-shadow: 3px 3px 15px -10px rgba(0, 0, 0, 0.5);
    border-radius: 50%;
}
.deleteIcon{
    width: 0.7em;
    height: 0.7em;
}
.note .btn:hover{
    background-color: #f5ba13;

}
.note .btn:hover .deleteIcon{
color: #fff;
}
footer{
    position: absolute;
    bottom: 10px;
    right: 50%;
    font-weight: 900;
    
}
